import MapMarker from 'vue-material-design-icons/MapMarker';
import AccountMultiplePlus from 'vue-material-design-icons/AccountMultiplePlus';
import Send from 'vue-material-design-icons/Send';
import { Button } from '@nextcloud/vue';
import Loader from '../components/Loader.vue';
import CollaboratorsSelectionForm from '../components/CollaboratorsSelectionForm.vue';
export default {
  name: 'AlbumCreationForm',
  components: {
    Button,
    MapMarker,
    AccountMultiplePlus,
    Send,
    Loader,
    CollaboratorsSelectionForm
  },
  props: {
    displayBackButton: {
      type: Boolean,
      default: false
    }
  },

  data() {
    return {
      showCollaboratorView: false,
      albumName: '',
      albumLocation: '',
      loading: false
    };
  },

  mounted() {
    this.$nextTick(() => {
      this.$refs.nameInput.focus();
    });
  },

  methods: {
    async createAlbum() {
      let collaborators = arguments.length > 0 && arguments[0] !== undefined ? arguments[0] : [];

      try {
        this.loading = true;
        const album = await this.$store.dispatch('createAlbum', {
          album: {
            name: this.albumName,
            location: this.albumLocation,
            collaborators
          }
        });
        this.$emit('album-created', {
          album
        });
      } finally {
        this.loading = false;
      }
    },

    back() {
      this.$emit('back');
    }

  }
};